<template>
  <v-layout row>
    <v-flex sm12>
      <v-card>
        <v-data-table
          :headers="headers"
          :items="tracks"
          class="elevation-1 tracks"
          hide-actions>
          <template slot="items" slot-scope="props">
            <tr v-on:click="trackSelected(props.item)">
              <td class="track-index">{{ props.index }}</td>
              <td class="track-title">{{ props.item.title }}</td>
              <td class="track-artist">{{ props.item.artist.name }}</td>
              <td class="track-play">
                <v-icon large v-on:click.stop="onPlayTrack(props.item)">play_arrow</v-icon>
              </td>
              <td class="track-edit">
                <v-menu bottom left @click.native.stop.prevent>
                  <v-btn slot="activator"
                         icon>
                    <v-icon>more_vert</v-icon>
                  </v-btn>
                  <v-list dense>
                    <v-list-tile>
                      <a :href="getTrackLocation(props.item)" target="_blank">
                        Open in WeTracker
                      </a>
                    </v-list-tile>
                  </v-list>
                </v-menu>
              </td>
              <td class="track-favourite">
                <v-btn
                  icon
                  flat
                  v-on:click.stop="onFavouriteTrack(props.item)">
                  <v-icon medium :color="props.item.is_favourite_of_current_user? 'red' : 'grey'">
                    favorite
                  </v-icon>
                </v-btn>
              </td>
              <td class="track-rating">
                <star-rating
                  @click.native.prevent.stop
                  v-model="props.item.average_rating"
                  :star-size="20"
                  v-on:rating-selected="setRating($event, props.item.id)">
                </star-rating>
              </td>
              <td class="track-remove">
                <v-btn v-on:click="onRemoveTrack(props.item.id)" icon>
                  <v-icon>delete</v-icon>
                </v-btn>
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from 'axios'
import StarRating from 'vue-star-rating'
import firebase from 'firebase'
import mixins from '../mixins.js'

export default {
  data () {
    return {
      tracks: [],
      headers: [
        {
          text: '#',
          sortable: false,
          class: 'track-index'
        },
        {
          text: 'Title',
          align: 'left',
          sortable: true,
          class: 'track-title',
          value: 'title'
        },
        {
          text: 'Artist',
          align: 'left',
          sortable: true,
          class: 'track-artist',
          value: 'artist.name'
        },
        {
          text: '',
          sortable: false,
          class: 'track-play'
        },
        {
          text: '',
          sortable: false,
          class: 'track-edit'
        },
        {
          text: '',
          sortable: false,
          class: 'track-favourite'
        },
        {
          text: 'Average Rating',
          align: 'right',
          sortable: true,
          value: 'average_rating'
        },
        {
          text: '',
          sortable: false,
          class: 'track-remove'
        }
      ]
    }
  },
  props: {
    user: Object,
    playlistId: Number
  },
  mixins: [
    mixins
  ],
  methods: {
    getPlaylistTracksFromBackend () {
      if (this.playlistId) {
        const path = process.env.API_BASE_URL + `api/playlists/${this.playlistId}/tracks`
        if (this.user) {
          var self = this
          this.user.getIdToken(true).then(function (idToken) {
            axios.get(path,
              { headers: { 'Authorization': 'bearer ' + idToken } })
              .then(response => {
                self.tracks = response.data
                self.$emit('num-tracks', self.tracks.length)
              })
              .catch(error => {
                console.log(error)
              })
          })
        } else {
          axios.get(path)
            .then(response => {
              this.tracks = response.data
              this.$emit('num-tracks', this.tracks.length)
            })
            .catch(error => {
              console.log(error)
            })
        }
      }
    },
    trackSelected (item, index) {
      this.$router.push({ name: 'track', params: { id: item.id } })
    },
    onRemoveTrack (trackId) {
      if (this.playlistId) {
        const user = firebase.auth().currentUser
        if (user) {
          const self = this
          user.getIdToken(true).then(function (idToken) {
            const path = process.env.API_BASE_URL + `api/playlists/${self.playlistId}/tracks/${trackId}`
            axios.delete(path, {
              headers: { 'Authorization': 'bearer ' + idToken }})
              .then(response => {
                self.getPlaylistTracksFromBackend()
              })
              .catch(error => {
                console.log(error)
              })
          })
        }
      }
    },
    onPlayTrack (track) {
      this.$emit('play-track', track.id)
    },
    getTrackLocation (track) {
      const playerRoot = 'http://app.wetracker.xyz/#/loadsong?url='
      var url = encodeURI('https://modland.com/pub/modules/Fasttracker 2/' + track.location)
      return playerRoot + url
    },
    setRating (rating, track) {
      const path = process.env.API_BASE_URL + `api/tracks/` + track + `/rate`
      const user = firebase.auth().currentUser
      const self = this
      if (user) {
        user.getIdToken(true).then(function (idToken) {
          self.$gtm.trackEvent({
            event: 'track-rate',
            action: 'rate',
            category: 'Track',
            label: 'Track Rated',
            track_id: track,
            rating: rating
          })
          axios.post(path, {
            rating: rating
          }, { headers: { 'Authorization': 'bearer ' + idToken } })
            .then(function (response) {
              console.log(response)
            }).catch(function (error) {
              console.log(error)
            })
        })
      }
    },
    onFavouriteTrack (track) {
      this.setFavourite(track).then(() => {
        this.getPlaylistTracksFromBackend()
      })
    }
  },
  components: {
    StarRating
  },
  created () {
    this.getPlaylistTracksFromBackend()
  }
}
</script>

<style scoped>
.tracks-list {
  display: flex;
  flex-direction: column;
  overflow: scroll;
  height: 0;
}
.tracks-list .table {
  margin-bottom: 0;
}
</style>
<style>
.tracks td.track-title, .tracks th.track-title {
  text-align: left;
  width: 50%;
}
.tracks td.track-artist, .tracks th.track-artist {
  text-align: left;
  width: 50%;
}
.tracks td.track-index, .tracks th.track-index {
  white-space: nowrap;
  width: 1px;
}
.tracks td.track-play, .tracks th.track-play {
  white-space: nowrap;
  width: 1px;
}
.tracks td.track-edit, .tracks th.track-edit {
  white-space: nowrap;
  width: 1px;
}
.tracks td.track-favourite, .tracks th.track-favourite {
  white-space: nowrap;
  width: 1px;
}
.tracks td.track-rating, .tracks th.track-rating {
  display: flex;
  justify-content: flex-end;
}
</style>



